﻿.quizContainer {
  position: relative;
  margin: 0px auto;
  background-color: #fff;
  background-image: url("../../img/bg-home.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 100%;
  height: 88vh;
  overflow-y: scroll;
  padding-bottom: 100px;
}

.quizQuestion {
  padding-top: 2em;
  font-size: 28px;
  font-weight: bolder;
  font-family: "Open Sans";
  color: "#5E5E5E";
}

.optionSection {
  padding-top: 3em;
  font-family: "Open Sans";
}

.optionSection .contain .checkmark::after {
  top: 9.3px;
  left: 9.5px;
  width: 20px;
  height: 20px;
}

.optionSection label {
  padding: 1em 0;
}

.optionSection label span.question {
  position: relative;
  display: inline-block;
  padding-left: 6%;
  bottom: 10px;
}

.optionSection .checkmark {
  height: 40px;
  width: 40px;
}

.optionSection input[type="radio"] {
  width: 50%;
}

.next-button {
  border-radius: 22px;
  padding-left: 35px;
  padding-top: 10px;
  padding-right: 35px;
  padding-bottom: 10px;
  background-color: #10a3ff;
  border: none;
  color: #fff;
  font-size: 14px;
  margin-right: 15px;
}

.viewpassage-btn{
  color:$studyblue;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 15px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  left: 11%;
  top: -3%

}

.viewpassage-btn:focus{
  outline: none !important; 
  border:none
}

.viewsection-btn{
  color:$studyblue;
  background-color: transparent;
  border-radius: 30px;
  padding:0.5em;
  outline: none;
  font-size: 13px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  top:3%;
  right: 15%;
  border: 3px solid $studyblue;

}

.viewsection-btn1{
  color:$studyblue;
  background-color: transparent;
  outline: none;
  font-size: 15px;
  font-weight: bolder;
  position: absolute;
  z-index: 999;
  cursor: pointer;
  top:-3%;
  right: 15%;
  border:none

}

.viewsection-btn:focus{
  outline: none !important;
}

.previous-button {
  border-radius: 22px;
  padding-left: 35px;
  padding-top: 10px;
  padding-right: 35px;
  padding-bottom: 10px;
  background-color: transparent;
  border: 1px solid #1e1e1e;
  color: $studyblue;
  font-size: 14px;
  margin-right: 15px;
}

.next-button:focus {
  outline: none;
}
.previous-button:focus {
  outline: none;
}

.section-bottom{
  position: fixed;
    z-index: 1001;
    background-color: #f5f5f5;
    bottom: 0;
    left: 0;
    color: black;
    width: 100%;
    height: 90vh;
    transition: 0.5s;

  .container{
    padding: 3em;
    font-size: 30px;
    color: black;
    text-align: justify;
    line-height: 60px;
  }
}
 
.section-hide{
  bottom: -85% !important;
}
.section-reset{
  bottom: 0 !important;
}